<template>
  <div>
  <!-- Menu de selección -->
   <div v-if="select == ''" >
    <nav>
    <div class="nav-wrapper">
      <a class="brand-logo center">OSX Installer Icons</a>
    </div>
  </nav>
    <center>
        <div style="margin: 13px 0;" ></div>  
        <button v-on:click="click('es')" class="waves-effect waves-light btn btn-large" >Español</button>
		    <div style="margin: 13px 0;" ></div>
        <button v-on:click="click('us')" class="waves-effect waves-light btn btn-large" >English</button>  

    </center>
   </div>
   <!-- end menu de selección -->
    <Es v-else-if="select == 'es'" ></Es>
    <Us v-else-if="select == 'us'" ></Us>
  </div>
</template>

<script>
import Es from "./es";
import Us from "./us";
export default {
  components: {
    Es,
    Us
  },
  data() {
    return {
      select: ""
    };
  },
  methods: {
    click(idioma) {
      this.select = idioma;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



