<template>
  <div>
    <Sl v-if="live"></Sl>
    <div v-else><center><h4></h4></center></div>
  </div>
</template>

<script>
import Sl from "./components/select-es-us";
import axios from "axios";

export default {
  name: "App",
  components: {
    Sl
  },
  data() {
    return {
      live: false
    };
  },
  created() {
    axios
      .get("http://localhost:8087/live")
      .then(response => {
        if (response.data === "live") {
          this.live = true;
        }
      })
      .catch(e => {
        alert("Error se esperava la respuesta del cliente");
      });
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



